<template>
  <div class="airdrop">
    <p class="text-20 text-white">使用TeaDrop将您的代币空投给您的所有用户</p>
    <div class="airdrop-content MT-20">
      <div v-click-outside="clickOutside" class="airdrop-type flex-row-between pointer MB-20" :class="{ 'type-show': typesShow }" @click="typesShow = !typesShow">
        <div class="airdrop-type__left text-left">
          <div class="text-16 text-999">空投类型</div>
          <div class="text-18 text-white MT-5">{{ type == 1 ? '其他代币' : 'BNB' }}</div>
        </div>
        <el-icon color="#fff" :size="16">
          <caret-bottom />
        </el-icon>
        <transition name="el-zoom-in-top">
          <div v-show="typesShow" class="airdrop-type__list">
            <div class="airdrop-type__list-item" @click="toggleType(1)">其他代币</div>
            <div class="airdrop-type__list-item" @click="toggleType(2)">BNB</div>
          </div>
        </transition>
      </div>
      <div class="airdrop-desc text-left text-16 text-999 MB-15">
        <div class="airdrop-desc__title text-18 text-white MB-10">空投说明：</div>
        <div class="MT-5">- 向任意数量的用户空投代币</div>
        <div class="MT-5">- 如果您正在进行销售，请确保代币没有空投</div>
        <div class="MT-5">- 先输入你的代币地址</div>
        <div class="MT-5">- 输入要空投的用户列表，后跟金额（逗号分隔）</div>
      </div>
      <div class="text-16 text-999 MB-15">空投费用:{{ utils.formatEther(feeAmount)}} BNB</div>
      <transition name="el-zoom-in-top">
        <div v-show="type == 1" class="airdrop-type text-left MB-15">
          <div class="text-16 text-999">请输入令牌地址</div>
          <div class="flex-row-start MT-5 align-center">
            <input v-model.trim="tokenAddr" class="airdrop-type__input text-18 text-white flex-1" placeholder="请输入令牌地址">
            <el-icon v-show="searchLoading" class="is-loading" color="#fff">
              <loading />
            </el-icon>
          </div>
        </div>
      </transition>
      <div v-show="type == 1" class="text-18 text-999 text-left">
        <span class="MR-5">代币:</span>
        <span class="text-yellow">{{ ERC20Info.symbol }}({{ ERC20Info.name || '--' }})</span>
      </div>
      <div class="flex-row-between align-center">
        <div class="text-18 text-999 text-left MT-10">
          <span class="MR-5">余额:</span>
          <span v-if="type == 1" class="text-yellow">{{ utils.formatUnits(ERC20Info.balance, ERC20Info.decimals) }}</span>
          <span v-else class="text-yellow">{{ utils.formatEther(ERC20Info.balance) }}</span>
        </div>
        <div class="flex-row-end">
          <input type="text" placeholder="请输入代币数量" class="airdrop-input text-16 MR-10" v-model.trim="price" />
          <el-tooltip
            class="box-item"
            effect="dark"
            content="统一发放数量，为空则按照默认规则发放"
            placement="top"
          >
            <el-icon><QuestionFilled /></el-icon>
          </el-tooltip>
        </div>
      </div>
      
      <div class="airdrop-list MT-20">
        <textarea v-model.trim="disListInfo" class="airdrop-list__textarea text-white text-18" placeholder="输入分发列表" />
      </div>
      <p class="text-yellow text-18 MT-10 MB-20">为了获得最佳效果，我们建议您一次最多处理500个地址！</p>
      <div class="text-18 text-999">
        空投的代币总数:
        <span class="text-yellow">{{ totalPrice }}</span>
      </div>
      <div class="flex-row-center MT-15">
        <el-button v-if="!isLogin" class="btn btn-line color-text MR-18" :loading="loginLoading" @click="loginHandler">Connect</el-button>
        <el-button v-else-if="isApprove == 2" class="btn btn-line color-text MR-18" :loading="approveLoading" @click="approveHandler">APPROVE</el-button>
        <el-button v-else-if="isApprove == 1" class="btn" :loading="airdropLoading" @click="airdropHandler">AIRDROP</el-button>
        <el-button v-if="isApprove == 0 && type == 2" class="btn" :loading="airdropLoading" @click="airdropETHHandler">AIRDROP</el-button>
      </div>
    </div>
  </div>
</template>

<script>
/* global wallet */
// import { CaretBottom, Loading } from '@element-plus/icons'
import { ElNotification } from 'element-plus'
import { debounce, Tips } from '@/libs/utils.js'
import { BigNumber, Contract, utils } from 'ethers'
import IERC20ABI from '@/api/abis/IERC20.json'
import ABI from '@/api/abis/SwapHelper.json'
import { product } from '@/plugins/wallet/config.json'

import { mapActions } from 'vuex'
window.Contract = Contract
window.IERC20ABI = IERC20ABI
let airDropContract = null
let contractERC20 = null
export default {
  // components: {
  //   CaretBottom,
  //   Loading
  // },
  data() {
    return {
      utils: utils,
      typesShow: false,
      // 当前选中类型
      type: 1,
      // 是否有登陆
      isLogin: false,
      // 是否有授权 0: 未知 1:已授权 2:未授权
      isApprove: 0,
      // 授权loading
      approveLoading: false,
      // 登陆loading
      loginLoading: false,
      feeAmount: BigNumber.from(0),
      // 合约地址
      tokenAddr: '',
      disListInfo: '',
      // 空投loading
      airdropLoading: false,
      searchLoading: false,
      price: '',
      ERC20Info: {
        name: '',
        symbol: '',
        decimals: 0,
        balance: BigNumber.from(0),
        allowance: BigNumber.from(0)
      }
    }
  },
  computed: {
    // 格式化列表
    disList() {
      let list = []
      const resList = []
      if (this.disListInfo) {
        list = this.disListInfo.split('\n')
      }
      for (let i = 0; i < list.length; i++) {
        const item = list[i]
        const arr = item.split(',')
        resList.push({
          addr: arr[0],
          price: this.price || arr[1]
        })
      }
      return resList
    },
    // 统计总金额
    totalPrice() {
      let totalPrice = 0
      this.disList.forEach((item) => {
        const price = item.price
        if (price && !isNaN(price)) {
          totalPrice += Number(price)
        }
      })
      return totalPrice
    }
  },
  watch: {
    tokenAddr: debounce(async function() {
      this.searchLoading = true
      // TODO 搜索接口
      try {
        const ERC20 = this.$wallet.initContract(this.tokenAddr, IERC20ABI)
        const [name, symbol, decimals, balance, allowance] = await Promise.all([
          ERC20.name(),
          ERC20.symbol(),
          ERC20.decimals(),
          ERC20.balanceOf(wallet.accounts[0]),
          ERC20.allowance(wallet.accounts[0], airDropContract.address)
        ])
        this.ERC20Info = { name, symbol, decimals, balance }
        this.isApprove = allowance.isZero() ? 2 : 1
        contractERC20 = ERC20
      } catch (error) {
        console.error(error)
        this.ERC20Info = {
          name: '',
          symbol: '',
          decimals: 0,
          balance: BigNumber.from(0),
          allowance: BigNumber.from(0)
        }
        this.isApprove = 2
        contractERC20 = null
      }
      this.searchLoading = false
    }, 500)
  },
  created() {
    this.isLoginFn()
    this.$wallet.on('accountChange', () => {
      this.isLoginFn()
    })
  },
  methods: {
    ...mapActions(['setConnectDialog']),
    // 是否登陆
    async isLoginFn() {
      if (this.$wallet.accounts[0]) {
        this.isApprove = 0
        this.isLogin = true
        airDropContract = window.wallet.initContract(product.SWAP_HELPER, ABI)
        airDropContract.callFee().then((val) => {
          this.feeAmount = val
        })
      } else {
        this.isLogin = false
      }
    },
    // 获取BNB余额
    getBnbBalance() {
      airDropContract.provider.getBalance(wallet.accounts[0]).then((data) => {
        this.ERC20Info = { name: 'BNB', balance: data }
      })
    },
    clickOutside() {
      this.typesShow = false
    },
    /**
     * 切换类型
     * @param {Number} type 当前选中类型 1:Aindrop Tokens 2:BNB
     */
    toggleType(type) {
      this.type = type
      this.ERC20Info = {
        name: '',
        symbol: '',
        decimals: 0,
        balance: BigNumber.from(0),
        allowance: BigNumber.from(0)
      }
      if (type === 2) {
        this.getBnbBalance()
      }
    },
    // 登陆
    loginHandler() {
      this.setConnectDialog(true)
    },
    // 授权
    async approveHandler() {
      // TODO 授权接口
      const bool = this.dataVerify()
      if (!bool) {
        return
      }
      this.approveLoading = true
      try {
        const res = await contractERC20.totalSupply()
        const tx = await contractERC20.approve(airDropContract.address, res)
        await tx.wait()
        this.isApprove = 1
      } catch (error) {
        console.error(error)
        Tips(error)
      }
      this.approveLoading = false
    },
    // 空投
    async airdropHandler() {
      this.airdropLoading = true
      // TODO 调用空投接口
      try {
        const { address } = contractERC20
        const addrs = this.disList.map((e) => e.addr)
        const amounts = this.disList.map((e) => utils.parseUnits(e.price.toString(), this.ERC20Info.decimals))
        // 初始值是手续费  求和数组
        const totalAmount = amounts.reduce((sum, item) => sum.add(item))
        const tx = await airDropContract.manyTransferToken(address, addrs, amounts, { value: this.feeAmount,  })
        await tx.wait()
        ElNotification({
          type: 'success',
          title: 'Success',
          message: 'Airdrop Success'
        })
      } catch (error) {
        console.error(error)
        Tips(error)
      }
      this.airdropLoading = false
    },
    // 空投发送BNB
    async airdropETHHandler() {
      this.airdropLoading = true
      // TODO 调用空投接口
      try {
        const addrs = this.disList.map((e) => e.addr)
        const amounts = this.disList.map((e) => utils.parseEther(e.price.toString()))
        // 初始值是手续费  求和数组
        const totalAmount = amounts.reduce((sum, item) => sum.add(item), this.feeAmount)
        const tx = await airDropContract.manyTransferETH(addrs, amounts, { value: totalAmount })
        await tx.wait()
        ElNotification({
          type: 'success',
          title: 'Success',
          message: 'Airdrop Success'
        })
      } catch (error) {
        console.error(error)
        Tips(error)
      }
      this.airdropLoading = false
    },
    // 授权数据校验
    dataVerify() {
      if (!this.tokenAddr) {
        ElNotification({
          type: 'error',
          title: 'Tips',
          message: 'Please Enter Token Address'
        })
        return false
      }
      if (this.disList.length <= 0) {
        ElNotification({
          type: 'error',
          title: 'Tips',
          message: 'Please Enter Distribution List'
        })
        return false
      }

      let status = true
      for (let i = 0; i < this.disList.length; i++) {
        const { addr, price } = this.disList[i]
        if (addr.substr(0, 2) !== '0x' || addr.length !== 42 || !price) {
          ElNotification({
            type: 'error',
            title: 'Tips',
            message: `The format of line ${i + 1} is wrong, please check the vehicle`
          })
          status = false
          break
        }
      }
      return status
    }
  }
}
</script>

<style lang="scss" scoped>
  
.btn {
  height: 38px;
  line-height: 38px;
  font-size: 16px;
  min-height: 0;
  background: linear-gradient(-68deg, #ffd60d, #dff522);
  border-radius: 19px;
  border: none;
  padding-top: 0;
  padding-bottom: 0;
  color: #3a382e;
  transition: all 0.3s;
  &.btn-line {
    border: 1px solid #ffd60d;
    color: #ffd60d;
    background: linear-gradient(-68deg, #978dbe 0%, #ffd60d 0%, #dff522 100%);
    -webkit-background-clip: text;
  }
  &:hover {
    transform: scale(0.95);
    color: #3a382e;
  }
}

.airdrop-input{
  background: none;
  border: 1px solid #2A2A2A;
  height: 30px;
  border-radius: 8px;
  color: #fff;
  padding: 0 10px;
  width: 130px;
}

.airdrop {
  width: 100%;
  max-width: 8rem;
  margin: 0 auto;
  text-align: center;
  .airdrop-content {
    width: 100%;
    border-radius: 20px;
    padding: 20px;
    border: 1px solid #2A2A2A;
    background-color: rgba(39, 39, 39, 0.8);
    .airdrop-type {
      padding: 5px 15px 7px;
      border: 1px solid rgba(255, 255, 255, 0.42);
      border-radius: 7px;
      position: relative;
      &.type-show {
        border-bottom-color: transparent;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
      }
      .airdrop-type__input {
        width: 100%;
        background: none;
      }
      .airdrop-type__list {
        position: absolute;
        left: -1px;
        right: -1px;
        top: 100%;
        background: #25241e;
        box-shadow: #393730 0 15px 15px;
        border: 1px solid rgba(255, 255, 255, 0.42);
        border-top: none;
        border-bottom-left-radius: 0.07rem;
        border-bottom-right-radius: 0.07rem;
        .airdrop-type__list-item {
          height: 50px;
          line-height: 50px;
          color: #fff;
          font-size: 14px;
          cursor: pointer;
          transition: all 0.3s;
          text-align: left;
          padding-left: 15px;
          &:hover {
            background: #636259;
          }
        }
      }
    }
    .airdrop-list {
      height: 200px;
      border: 1px solid #ffffff;
      border: 1px solid rgba(255, 255, 255, 0.42);
      border-radius: 7px;
      padding: 10px;
      .airdrop-list__textarea {
        width: 100%;
        height: 100%;
        background: none;
        resize: none;
      }
    }
  }
}
</style>
