<template>
  <div class="swap-info PB-20">
    <div class="swap-top MB-20">
      <div class="swap-info__title MB-10">抢购预售</div>
      <div class="swap-info__title-info">按指定条件自动购买预售</div>
      <img src="@/assets/images/rpc.png" class="swap-top__icon auto-rpc pointer" @click="openRpcList" />
    </div>

    <div class="contract-box ML-20 MR-20">
      <input 
        type="text" 
        class="contract-address" 
        :disabled="isOpen" 
        v-model="contractAddress" 
        placeholder="请输入预售合约地址"
        @input="contractAddressChange"
      >
      <el-icon class="is-loading contract-box__loading" v-if="detailLoading">
        <Loading />
      </el-icon>
    </div>
    <div class="MT-10 MB-10 PL-20 PR-20" v-if="coinDetail.symbol">
      <div class="flex-row-start presale-item MB-5">
        <div class="presale-item__label">预售代币：</div>
        <div class="presale-item__value flex-1">{{coinDetail.symbol || '--'}}</div>
      </div>
      <div class="flex-row-start presale-item MB-5">
        <div class="presale-item__label">代币合约：</div>
        <div class="presale-item__value flex-1">{{coinDetail.token || '--'}}</div>
      </div>
      <div class="flex-row-start presale-item MB-5">
        <div class="presale-item__label">买入最大限额：</div>
        <div class="presale-item__value flex-1 text-red">{{coinDetail.max ? `${toUnits(coinDetail.max)} BNB` : '--'}}</div>
      </div>
      <div class="flex-row-start presale-item MB-5">
        <div class="presale-item__label">买入最小限额：</div>
        <div class="presale-item__value flex-1 text-red">{{coinDetail.min ? `${toUnits(coinDetail.min)} BNB` : '--'}}</div>
      </div>
      <div class="flex-row-start presale-item MB-5">
        <div class="presale-item__label">软硬顶：</div>
        <div class="presale-item__value flex-1">{{coinDetail.softCap ? `${toUnits(coinDetail.softCap)} BNB - ${toUnits(coinDetail.hardCap)} BNB` : '--'}}</div>
      </div>
      <div class="flex-row-start presale-item MB-5">
        <div class="presale-item__label">开始时间：</div>
        <div class="presale-item__value flex-1">{{coinDetail.startTime ? moment(new Date(coinDetail.startTime.toNumber() * 1000)).format('YYYY-MM-DD HH:mm:ss') : '--'}}</div>
      </div>
      <div class="flex-row-start presale-item">
        <div class="presale-item__label">结束时间：</div>
        <div class="presale-item__value flex-1">{{coinDetail.endTime ? moment(new Date(coinDetail.endTime.toNumber() * 1000)).format('YYYY-MM-DD HH:mm:ss') : '--'}}</div>
      </div>
      <div class="flex-row-start presale-item" v-if="availableQuantity.gt(zero)">
        <div class="presale-item__label">可领取数量：</div>
        <div class="presale-item__value flex-1">{{ availableQuantity }}</div>
      </div>
    </div>
    <div class="PL-20 PR-20 MT-10">
      <div class="flex-row-between align-center MB-10">
        <div class="flex-row-start buy-item align-center flex-1">
          <span>发送代币数量</span>
          <input type="text" placeholder="0" class="buy-item__input flex-1" :disabled="isOpen" v-model="number"/>
        </div>
      </div>
      <div class="flex-row-between align-center MB-10">
        <div class="flex-row-start buy-item align-center">
          <span>Gas Limit</span>
          <input type="text" placeholder="0" class="buy-item__input flex-1" :disabled="isOpen" v-model="gasLimit" />
        </div>
        <div class="flex-row-start buy-item align-center">
          <span>Gas Fee</span>
          <input type="text" placeholder="0" class="buy-item__input flex-1" disabled :value="gasFee" />
        </div>
      </div>
      <div class="flex-row-between align-center">
        <div class="flex-row-start buy-item align-center">
          <span>每次发送交易数</span>
          <input type="text" placeholder="0.00" class="buy-item__input flex-1" :disabled="isOpen" v-model="sendNum"/>
        </div>
        <div class="flex-row-start buy-item align-center">
          <span>时间间隔</span>
          <input type="text" placeholder="0" class="buy-item__input flex-1" :disabled="isOpen" v-model="timeInterval"/>
        </div>
      </div>
    </div>
    <div class="buy-tips MT-15 PL-20 PR-20">开启指定时间执行时,请依照网速提前2-4秒开始！</div>
    <div class="flex-row-start align-center PL-20 PR-20 MT-10 MB-10">
      <div class="flex-row-start align-center MR-20">
        指定时间执行
        <el-switch v-model="openDate" class="ML-10" :disabled="isOpen"/>
      </div>
      <el-time-picker
        v-model="date"
        class="flex-1"
        is-range
        size="small"
        v-if="openDate"
        :disabled="isOpen"
        :editable="false"
        range-separator="-"
        start-placeholder="开始时间"
        end-placeholder="结束时间"
      />
    </div>
    <div class="swap-slippage PL-20 PR-20 MT-10">
      <div class="swap-slippage__select flex-row-between pointer">
        <div class="text-16">GWEI</div>
        <div class="flex-row-end">
          <el-tag class="swap-slippage__select-tag" type="warning" :effect="gWei == item ? 'dark' : 'light'" @click="toggleGwei(item)" v-for="item in gWeiList" :key="item">
            {{ item }}
          </el-tag>
          <input type="number" class="swap-slippage__select-input" placeholder="5" v-model.number="gWei" />
        </div>
      </div>
    </div>
    <div class="buy-tips MT-15 PL-20 PR-20">抢购使用烧区块模式，请确保BNB余额充足,并且刚好只够买中一次,否则会成功买中多次。</div>
    <div class="buy-tips MT-15 PL-20 PR-20">由于烧区块模式产生大量交易无法一一验证交易是否成功,请自行检查是否成功烧到并及时关闭脚本,以防止产生不必要的浪费。</div>
    <el-button v-if="!isLogin" class="swap-btn MT-15" @click="loginFn">{{ t('swap.connectWallet') }}</el-button>
    <el-button v-else-if="!hasPledge" class="swap-btn MT-15" @click="pledgeHandler">成为股东使用该功能</el-button>
    <el-button v-else-if="!coinDetail.token" disabled class="swap-btn MT-15">请输入正确合约</el-button>
    <el-button v-else-if="!isWhiteList" class="swap-btn MT-15" disabled>非白名单用户不能购买</el-button>
    <el-button v-else-if="availableQuantity.gt(zero)" class="swap-btn MT-15" disabled>不能重复购买</el-button>
    <el-button v-else-if="circulation > 0" class="swap-btn MT-15" :loading="claimLoading" @click="claimHandler">领取</el-button>
    <el-button v-else class="swap-btn MT-15" :loading="isOpen" @click="startHandler">
      <div class="flex-row-center">
        <el-icon class="is-loading MR-5" v-if="isOpen">
          <Loading />
        </el-icon>
        <span>{{ isOpen ? '停止' : '开始' }}</span>
      </div>
    </el-button>
  </div>
  <RpcList ref="rpcList" />
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import PRESALE_ABI from '@/api/abis/presale.json'
import { utils } from 'ethers'
import { getBit, Tips } from '../../libs/utils'
import { ElMessageBox, ElNotification } from 'element-plus'
import RpcList from '../swap/modal/rpcList.vue'
import moment from 'moment'
import POOL_ABI from '../../api/abis/pool'
import PAIR_ABI from '@/api/abis/getPair.js'
const zero = utils.parseUnits('0', 18)
export default {
  components: {
    RpcList
  },
  data() {
    return {
      utils,
      zero,
      moment,
      getBit,
      // 是否指定时间开启
      openDate: false,
      // 指定时间
      date: [],
      isLogin: false,
      contract: null,
      // 时间间隔
      timeInterval: 3,
      // 一次发送几个包
      sendNum: 1,
      gWei: 5,
      gWeiList: [5, 7, 10, 15, 20],
      // 合约地址
      contractAddress: '', //'0x829e67f327b7283C44734518C7E83b135a703b74',
      gasLimit: '800000',
      // 是否登录
      loading: false,
      // 当前用户是否是白名单
      isWhiteList: false,
      // 购买数量
      number: '',
      // 当前是否已开启
      isOpen: false,
      detailLoading: false,
      // 当前合约代币信息
      coinDetail: {},
      timer: null,
      // 可领取数量
      availableQuantity: zero,
      // 当前流通量
      circulation: 0,
      // 领取loading
      claimLoading: false
    }
  },
  watch: {
    'currentNetwork.ChainID': {
      immediate: true,
      handler() {
        const { Token } = this.currentNetwork
        const item1 = Token[0]
        const item2 = Token[1] || Token[0]
        this.item1 = { ...this.item1, ...item1 }
        this.item2 = { ...this.item2, ...item2 }
        this.gWei = 5 * this.currentNetwork.GWEI
        this.gWeiList = [5 * this.currentNetwork.GWEI, 7 * this.currentNetwork.GWEI, 10 * this.currentNetwork.GWEI, 15 * this.currentNetwork.GWEI, 20 * this.currentNetwork.GWEI]
      }
    }
  },
  computed: {
    ...mapGetters(['currentNetwork', 'networkList', 'currentDex', 'hasPledge']),
    gasFee() {
      if (!isFinite(this.gasLimit) || !isFinite(this.gWei)) {
        return 0
      }
      return this.gasLimit * this.gWei / 1000000000
    },
  },
  mounted() {
    if (window.wallet.accounts[0] && window.wallet.connectState == 3) {
      this.isLogin = true
    }
    this.isLogin && this.loginCallBack()
    window.wallet.on('accountChange', () => {
      if (window.wallet.accounts[0] && window.wallet.connectState == 3) {
        this.isLogin = true
      } else {
        this.isLogin = false
      }
      this.isLogin && this.loginCallBack()
    })
  },
  methods: {
    ...mapActions(['setConnectDialog']),
    pledgeHandler() {
      ElMessageBox.confirm(
          '需要质押$50资产，或者10个下级质押过$10资产才能使用该功能',
          'Warning',
          {
              confirmButtonText: '质押',
              cancelButtonText: '取消',
              type: 'warning',
          }
      ).then(() => {
          this.$router.push({
              name: 'stake'
          })
      })
    },
    toUnits(num) {
      return Number(utils.formatUnits(num, 18))
    },
    // 登陆
    loginFn() {
      ElMessageBox.prompt('请输入私钥', 'Tip', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
      }).then(({value}) => {
        window.wallet.connect(3, value)
      })
    },
    // 获取流动性池子信息
    async getPondData() {
      const BNB_ADDRESS = '0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c'
      const pairContract = window.wallet.initContract('0xcA143Ce32Fe78f1f7019d7d551a6402fC5350c73', PAIR_ABI)
      const data = await pairContract.getPair(
        this.coinDetail.token, 
        BNB_ADDRESS
      )
      const contract = window.wallet.initContract(data, POOL_ABI)
      const token0 = await contract.token0()
      const token1 = await contract.token1()
      const res = await contract.getReserves()
      if (token0.toLocaleLowerCase() === BNB_ADDRESS.toLocaleLowerCase()) {
        this.circulation = res._reserve1
      } else if (token1.toLocaleLowerCase() === BNB_ADDRESS.toLocaleLowerCase()) {
        this.circulation = res._reserve0
      }
    },
    // 开抢
    startHandler() {
      // 开启中就关闭
      if (this.isOpen) {
        clearInterval(this.timer)
        this.isOpen = false
      } else {
        let message = ''
        if (!this.number) {
          message = '请输入发送数量'
        }
        if (!this.gasLimit) {
          message = '请输入Gas Limit'
        }
        if (!this.sendNum) {
          message = '请输入每次发送交易数'
        }
        if (!this.timeInterval) {
          message = '请输入时间间隔'
        }
        if (message) {
          ElNotification({
            type: 'error',
            title: '温馨提示',
            message
          })
          return
        }
        // 如果当前是指定时间段执行
        if (this.openDate) {
          if (this.date.length <= 0) {
            ElNotification({
              type: 'error',
              title: '温馨提示',
              message: '请选择执行时间段'
            })
            return
          } else {
            let [start, end] = this.date
            start = (new Date(start)).valueOf()
            end = (new Date(end)).valueOf()
            const timer = setInterval(() => {
              const now = (new Date()).valueOf()
              if (now >= start && now <= end) {
                for (let i = 0; i < this.sendNum; i++) {
                  this.buyConfirm()
                }
                this.timer = setInterval(() => {
                  for (let i = 0; i < this.sendNum; i++) {
                    this.buyConfirm()
                  }
                }, this.timeInterval)
                clearInterval(timer)
              } else if (now > end) {
                clearInterval(timer)
                clearInterval(this.timer)
              }
            }, 1000)
            
          }
        } else {
          for (let i = 0; i < this.sendNum; i++) {
            this.buyConfirm()
          }
          this.timer = setInterval(() => {
            for (let i = 0; i < this.sendNum; i++) {
              this.buyConfirm()
            }
          }, this.timeInterval)
        }
      }
    },
    // 合约地址变化
    contractAddressChange() {
      if (this.contractAddress && this.contractAddress.length == 42) {
        this.contract = window.wallet.initContract(this.contractAddress, PRESALE_ABI)
        this.getCoinDetail()
        this.isWhiteListFn()
        this.getAvailableQuantity()
      }
    },
    // 打开rpclist
    openRpcList() {
      if (this.isOpen) {
        return
      }
      this.$refs.rpcList.open(window.wallet.connectState)
    },
    toggleGwei(data) {
      if (this.isOpen) {
        return
      }
      this.gWei = data
    },
    loginCallBack() {
      this.isWhiteListFn()
      this.getAvailableQuantity()
    },
    // 判断当前用户是否是白名单
    async isWhiteListFn() {
      if (!this.isLogin || !this.contract) {
        this.isWhiteList = false
        return
      }
      try {
        //获取白名单数量
        const num = await this.contract.getNumberOfWhitelistedUsers()
        if (num.gt(zero)) {
          const list = await this.contract.getWhitelistedUsers(zero, num)
          const bool = list.find((item) => {
            return item === window.wallet.accounts[0]
          })
          this.isWhiteList = !!bool
        } else {
          this.isWhiteList = false
        }
      } catch (error) {
        Tips(error)
      }
    },
    // 获取代币信息
    async getCoinDetail() {
      this.detailLoading = true
      try {
        const [one, two, three] = await Promise.all([
          this.contract.poolSettings(), 
          this.contract.poolStates(), 
          this.contract.getContributionSettings(),
        ])
        const { token, startTime, endTime, hardCap, softCap } = one
        const { max, min } = three
        const toolContract = window.wallet.initContract(token)
        const symbol = await toolContract.symbol()
        this.coinDetail = {
          token,
          max,
          min,
          symbol,
          startTime,
          endTime,
          hardCap,
          softCap
        }
        this.getPondData()
      } catch (error) {
        Tips(error)
      }
      this.detailLoading = false
    },
    // 购买代币
    async buyConfirm() {
      try {
        const tx = await this.contract.contribute({
          value: utils.parseUnits(this.number, 18),
          gasPrice: utils.parseUnits(this.gWei.toString, 9),
          gasLimit: this.gasLimit
        })
        await tx.wait()
        ElNotification({
          type: 'success',
          title: 'Success',
          message: '购买成功'
        })
        this.isOpen = false
        clearInterval(this.timer)
      } catch (error) {
        Tips(error) 
      }
    },
    // 领币
    async claimHandler() {
      this.claimLoading = true
      try {
        await this.contract.claim()
        this.availableQuantity = zero
        ElNotification({
          type: 'success',
          title: 'Success',
          message: '领取成功'
        })
      } catch (error) {
        Tips(error)
      }
      this.claimLoading = false
    },
    // 获取可领取数量
    async getAvailableQuantity() {
      if (!this.isLogin || !this.contract) {
        this.availableQuantity = zero
        return
      }
      this.contract.claimedOf(window.wallet.accounts[0]).then(data => {
        this.availableQuantity = data
      })
    }
  }
}
</script>

<style lang="scss" scoped>

.swap-slippage__select{
  padding: 10px !important;
}
.buy-tips{
  font-size: 12px;
  color: rgba(205, 196, 177, 1);
  &.red{
      color: rgba(244, 79, 46, 1);
      &::before{
          background: rgba(244, 79, 46, 1);
      }
  }
  &::before{
      content: "";
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: rgba(252, 190, 47, 1);
      display: inline-block;
      margin-right: 6px;
  }
}

.contract-box{
  position: relative;
  border: 1px solid #888;
  border-radius: 8px;
  width: calc(100% - 40px);
  .contract-box__loading{
    position: absolute;
    right: 10px;
    top: 50%;
    margin-top: -7px;
  }
}
.contract-address{
  height: 46px;
  width: 100%;
  background: none;
  color: #fff;
  padding: 0 10px;
}
.presale-item__value{
  font-weight: bold;
  word-break: break-all;
  &.text-red{
    color: #991B1B;
  }
}
.buy-item{
  width: calc(50% - 5px);
  height: 40px;
  border: 1px solid #888;
  border-radius: 8px;
  padding: 0 10px;
  margin-right: 10px;
  &:last-child {
    margin-right: 0;
  }
  .buy-item__input{
    background: none;
    text-align: right;
    color: #fff;
  }
}

.swap-btn{
    margin: 0 auto;
    width: calc(100% - 40px);
    max-width: 450px;
    height: 60px;
    background: #FCBE2F;
    border: 1px solid #2A2A2A;
    border-radius: 18px;
    color: #fff;
    display: block;
    &.is-disabled{
      background: #e4e7ed !important;
    }
    &:hover, &:focus, &:active{
      background: #FCBE2F;
      color: #fff;
      border: none;
    }
  }

</style>

